.loading{position:fixed;z-index:99;width:100%;height:100%;top:0;left:0;background:#f1f1f1;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center
}
