}
.loading .text{margin:0 auto;background:#ccc;width:120px;height:50px;-webkit-clip-path:url(#svgTextPath);clip-path:url(#svgTextPath);position:relative;overflow:hidden;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center
