rect#shape{fill:transparent;stroke:var(--base);stroke-width:2px;-webkit-animation:dong 0.5s linear infinite;animation:dong 0.5s linear infinite
}
@-webkit-keyframes dong{
0%{stroke-dasharray:100 240;stroke-dashoffset:0;stroke-opacity:0.1
}
50%{stroke-dasharray:200 200;stroke-dashoffset:-170;stroke-opacity:0.3
}
100%{stroke-dasharray:100 240;stroke-dashoffset:-340;stroke-opacity:1
}
}
@keyframes dong{
0%{stroke-dasharray:100 240;stroke-dashoffset:0;stroke-opacity:0.1
}
50%{stroke-dasharray:200 200;stroke-dashoffset:-170;stroke-opacity:0.3
}
100%{stroke-dasharray:100 240;stroke-dashoffset:-340;stroke-opacity:1
}
}

